                li.submenu__subitem
                  a.subitem__link(href='#') Leggings
                li.submenu__subitem
                  a.subitem__link(href='#') Jardineras
                li.submenu__subitem
                  a.subitem__link(href='#') Faldas
                li.submenu__subitem
                  a.subitem__link(href='#') Ropa Interior
                li.submenu__subitem
                  a.subitem__link(href='#') Pantalones cortos
                li.submenu__subitem
