    ul.menu-social__list
      li.menu-social__item(
        v-for='items in footer.children')
        a.foot-nav__link(
          :href='items.url',
          :class='items.icon',
          :target='items.target')
          span {{ items.name }}
</template>

<script>
import axios from 'axios'
export default {
  name: 'PageHeaderMenuMobile',
  data () {
    return {
      active: false,
      show: false,
      level1: undefined,
      level2: undefined,
      footer: {},
      nameFooter: undefined
    }
  },
  async created () {
    await axios.get('https://prilov.aguayo.co/api/menus/footer', {
    })
      .then(response => {
        this.footer = response.data.items[3]
        this.nameFooter = response.data.items[3].name
      })
      .catch(e => {
        console.log('ERROR : ' + e)
      })
  },

  methods: {
    MenuClose: function () {
      this.$emit('MenuClose')
    },
    toggleNav: function () {
      this.active = !this.active
    }
  }

}
</script>



